<template>
  <div class="flex-1"
    v-loading="loading"
    element-loading-text="定位中"
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)"
  >
    <div class="scroll result">
    {{a}}
      <div class="result-item" :key="item.title" v-for="item in geocoderResult">
        <div class="result__title">{{item.title}}</div>
        <div class="result__address">{{item.address}}</div>
      </div>
    </div>
    <div class="amap-view" id="container"></div>
  </div>
</template>

<script>
import AMap from 'AMap' // 引入高德地图
export default {
  name: 'BaiduMaps',
  components: {
  },
  data () {
    return {
      a: {},
      loading: false,
      geocoderResult: []
    }
  },
  mounted () {
    this.initial()
  },
  methods: {
    initial () {
      this.loading = true
      const map = new AMap.Map('container', {
        resizeEnable: true,
        center: [116.397428, 39.90923],
        zoom: 13
      })
      AMap.plugin('AMap.Geolocation', () => {
        const geolocation = new AMap.Geolocation({
          enableHighAccuracy: true, // 是否使用高精度定位，默认:true
          timeout: 10000, // 超过10秒后停止定位，默认：5s
          position: 'RB', // 定位按钮的停靠位置
          buttonOffset: new AMap.Pixel(10, 20), // 定位按钮与设置的停靠位置的偏移量，默认：Pixel(10, 20)
          zoomToAccuracy: true // 定位成功后是否自动调整地图视野到定位点
        })
        map.addControl(geolocation)
        geolocation.getCurrentPosition((status, result) => {
          console.log(status, result)
          this.a = {
            status, result
          }
          if (status === 'complete') {
            AMap.plugin('AMap.Geocoder', () => {
              const geocoder = new AMap.Geocoder()

              geocoder.getAddress([result.lng, result.lat], (status, result) => {
                if (status === 'complete' && result.info === 'OK') {
                  // result为对应的地理位置详细信息
                  this.a = result
                }
              })
            })
          }
          this.loading = false
        })
      })
    }
  }
}
</script>

<style scoped lang="less">
.amap-view {
  height: 100%;
  ::v-deep .amap-logo,
  ::v-deep .amap-copyright {
    visibility: hidden!important;
  }
}
.result {
  max-height: 30vh;
  padding: 10px;
  position: absolute;
  right: 0;
  top: 0;
  left: 0;
  z-index: 2;
  background: rgba(255,255,255,.9);
  overflow: auto;
}
.result-item + .result-item {
  margin-top: 10px;
}
.result__title {
  font-weight: bold;
}
.result__address {}
</style>
